import Head from "next/head";
import { useState } from "react";
import { useRouter } from "next/router";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const router = useRouter();

  async function submit(e) {
    e.preventDefault();
    setError("");
    const res = await fetch("/api/auth/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password })
    });
    const data = await res.json();
    if (data.ok) {
      router.push("/");
    } else {
      setError(data.message || "Login failed");
    }
  }

  return (
    <main className="auth-page">
      <Head><title>Sign in — Staff Scheduler</title></Head>
      <div className="auth-card">
        <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
          <div className="auth-logo">SO</div>
          <div>
            <h1 className="auth-title">Sign in</h1>
            <div className="auth-sub">Welcome back — sign in to your account</div>
          </div>
        </div>

        <form onSubmit={submit} style={{ marginTop: 12 }}>
          <div className="form-row">
            <label className="form-label">Email</label>
            <input className="form-input" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="your@email.com" />
          </div>
          <div className="form-row">
            <label className="form-label">Password</label>
            <input className="form-input" type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Enter your password" />
          </div>
          {error && <div className="error">{error}</div>}

          <div className="auth-actions">
            <button className="btn full">Sign in</button>
            <a className="muted-link" href="/signup">Don't have an account?</a>
          </div>
        </form>
      </div>
    </main>
  );
}


